import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Container from './pages/Container';

function App() {
  return (
    <Router>
        <Switch>
          <Route path="/">
            <Container />
          </Route>
        </Switch>
    </Router>
  );
}

export default App;
